* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'open sans', sans-serif;
  font-size: 20px;
}


div.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  width: 100%;
  margin: 0 auto;
  color: #222;
  text-align: center;
  background-color: #f0f0f0;
  font-family: "Comfortaa", cursive;
}
header {
  -webkit-flex-basis: 40vh;
          flex-basis: 40vh;
  position: relative;
  overflow: hidden;
  width: 100%;
}
section {
  margin: 0 auto;
  margin-top: 100px;
  margin-bottom: 100px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
  padding: 30px 0;
  width: 60%;
  background-color: white;
}

p {
  line-height: 200%;
  font-size: 20px;
  color: #424242;
  width: 80%;
  margin: 0 auto;
}
li {
  list-style-type: none;
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
  width: 100%;
  background-size: cover;
}

.main ul li {
  text-align: center;
  list-style-type: none;
  display: inline-block;
  padding: 8px;
  margin-left: 30px;
}
nav.main a {
  display: inline-block;
  text-decoration: none;
  color: #1b1b2b;
  text-transform: uppercase;
  font-size: 20px;
  padding: 10px;
}

nav.main a:hover {
  color: gray;
  border-bottom: 1px solid #ed2d2d;
}
nav.main a.active {
  color: #ed2d2d;
  border-bottom: 1px solid #f0f0f0;
}

.main {
  width: 70%;
}

.nameAndSurname {
  width: 50%;
  text-align: left;
  margin-left: 50px;
  font-size: 25px;
  color: #ed2d2d;
  -webkit-transform: translateY(7px);
          transform: translateY(7px);
}

.navigation {
  display: -webkit-flex;
  display: flex;
}

.aboutMe {
  padding: 10%;
  text-align: center;
}
.imgAboutMe {
  padding: 20px;
  width: 50%;
}
i {
  line-height: 200%;
  font-style: italic;
}

h1 {
  display: table;
  font-size: 17px;
  width: 27%;
  margin: 0 auto;
  text-align: left;
}
.skills {
  padding: 10%;
  text-align: center;
  font-family: "Comfortaa", cursive;
  color: #424242;
}
.imgSkills {
  width: 30%;
}
.skillsFooter {
  margin: 0 auto;
  width: 80%;
  font-size: 20px;
}
.skillsItem {
  margin-bottom: 25px;
}

.products {
  padding: 10%;
  text-align: center;
  color: #424242;
}
.gitHub {
  text-decoration: none;
  color: #ed2d2d;
}
.gitHub:hover {
  font-weight: bold;
}
.projectsFooter {
  margin: 0 auto;
  width: 80%;
  font-size: 20px;
}

.project {
  border-bottom: 1px solid #424242;
  margin-bottom: 20px;
  padding: 20px;
}
.name {
  font-size: 25px;
  font-weight: bold;
}
.description {
  margin: 0 auto;
  width: 70%;
  font-size: 20px;
  font-style: italic;
}
.imgProject {
  width: 40%;
  margin: 0 auto;
}
.linkProject {
  text-decoration: none;
  margin: 0 auto;
  display: block;
  font-size: 25px;
  padding: 20px;
  color: #ed2d2d;
}

.linkProject:hover {
  font-weight: bold;
  -webkit-animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97);
          animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97);
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
@-webkit-keyframes shake {
  10%,
  90% {
    -webkit-transform: translate3d(-3px, 0, 0);
            transform: translate3d(-3px, 0, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}
@keyframes shake {
  10%,
  90% {
    -webkit-transform: translate3d(-3px, 0, 0);
            transform: translate3d(-3px, 0, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

.contact {
  padding: 10%;
  text-align: center;
  color: #424242;
}
.contactHeader {
  margin: 0 auto;
  width: 80%;
}
.signature {
  font-family: "Comfortaa", cursive;
  font-family: "Sacramento", cursive;
  text-align: right;
  font-size: 40px;
  width: 100%;
  margin-top: 40px;
}
.contactList {
  margin: 0 auto;
  padding: 30px;
  width: 80%;
  text-align: left;
}

.icon {
  -webkit-transform: translateY(7px);
          transform: translateY(7px);
  padding: 10px;
  font-size: 40px;
  margin-right: 20px;
}
.contactLink {
  text-decoration: none;
  color: #424242;
}
.contactLink:hover {
  text-decoration: none;
  color: #ed2d2d;
}

